* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  font-size: 22px;
  font-weight: 500;
  font-family: sans-serif;
  margin: 0;
  padding: 0;
}

.country-list li {
  display: flex;
  flex-direction: row;
  margin-left: 25px;
  margin-bottom: 20px;
}

.country-list img {
  width: 35px;
  height: 35px;
  margin-right: 15px;
}